@import '../../_variables.scss';

.subscribe-button {
    width: 195px;
    height: 60px;
    background-color: #1D1D1F;

    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 16px;
    border:0;
    font-weight: 800;
    padding: 24px 51px 26px 51px;

    @media #{$mq-medium} {
        padding: 4px 51px 6px 51px;
    }

    @media #{$mq-small} {
        width: 175px;
    }
    
    @media #{$mq-extra-small} {
        margin: auto;
        height: 39px;
        width: 280px;
    }
}