@import '../../_variables.scss';

.unsubscribed-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;


    @media #{$mq-medium} {
        margin-bottom: 40px;
    }

    @media #{$mq-small} {
        margin-bottom: 0px;
    }

    h1 {
        font-weight: 300;
        font-size: 54px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        padding-top: 80px;
        padding-bottom: 80px;

        @media #{$mq-large} {
            font-size: 40px;
        }

        @media #{$mq-medium} {
            width: 500px;
        }

        @media #{$mq-small} {
            font-size: 32px;
        }

        @media #{$mq-extra-small} {
            width: 345px;
        }
    }

    h2 {
        font-weight: 400;
        text-align: center;
        font-size: 20px;
        line-height: 27px;
        margin: auto;
        width: 566px;
        padding-bottom: 20.5px;

        @media #{$mq-medium} {
            margin: auto;
            padding-left: 0px;
            padding-right: 0px;
            width: 480px;
        }
        @media #{$mq-small} {
            margin: auto;
            font-size: 16px;
            width: 280px;
        }
        @media #{$mq-extra-small} {
            width: 345px;
        }
    }

    .subscribe-form {
        align-self: center;
        padding-bottom: 130px;

        @media #{$mq-medium} {
            padding-bottom: 40px;
        }
    }
}