@import '../../_variables.scss';
.input-text{

     .input-text-field {
        width: 345px;
        height: 59px;
        background: #FFFFFF;
        border: 1px solid #E1E5EB;
        padding-left: 20px;
        
        @media #{$mq-medium} {
            width: 245px;
        }
        @media #{$mq-small} {
            width: 200px;
        }
        @media #{$mq-extra-small} {
            margin-bottom: 10px;
            height: 39px;
            width: 280px;
        }
    }

    .input-text-field::placeholder {
        font-size: 11px;
        color: #7C8CA5;
        
        padding-left: 21px;
    }

    .error {
        font-weight: 700;
        color: red;
        padding-top: 10px;
        padding-left: 20px;
    }
}