.signup-form {
    width: 445px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 23px;
    
    h1 {
        font-weight: 600;
        font-size: 32px;
        line-height: 42px;
        letter-spacing: 1.3px;

        padding-bottom: 32px;
    }

    a {
        letter-spacing: 0.5px;
        color: #75787C;
        padding-top: 12px;
        padding-bottom: 43px;
    }
    
    .signup-names-input {
        width: 100%;
        display: flex;
        justify-content: space-between;

        .sign-input {
            width: 48%;

            & input {
                width: 100%;
            }
        }
    }

    .date-of-birth {
        width: 100%;
        display: flex;
        justify-content: space-between;

        .sign-input {
            width: 32%;

            & input {
                width: 100%;
            }

            input[type=number]::-webkit-inner-spin-button, 
            input[type=number]::-webkit-outer-spin-button { 
                -webkit-appearance: none; 
                margin: 0; 
            }

            input[type=number] {
                -moz-appearance:textfield;

            }
        }
    }

    .country {
        
        select{
        margin-bottom:20px;
        width: 445px;
        height: 63px;
        border: 0px;
        border-bottom: 3px solid #E8E8E8;
        outline: none;
        }
        
        select::placeholder {
        color: rgba(0, 0, 0, 0); 
        }
        
        select:focus {
        border-bottom: 3px solid #A835D5
        }
        
        .input-label {
        display: block;
        position: relative;
        max-height: 0;
        font-weight: 500;
        pointer-events: none;
        }
        
        .input-label::before {
        color: var(--label__color);
        content: attr(data-content);
        display: inline-block;
        filter: blur(0);
        backface-visibility: hidden;
        transform-origin: left top;
        transition: transform 0.2s ease;
        left: 1rem;
        position: relative;
        outline: none;
        }
        
        .input-label::after {
        bottom: 1rem;
        content: "";
        height: 0.1rem;
        position: absolute;
        transition: transform 180ms cubic-bezier(0.4, 0, 0.2, 1),
            opacity 180ms cubic-bezier(0.4, 0, 0.2, 1), background-color 0.3s ease;
        opacity: 0;
        left: 0;
        top: 100%;
        margin-top: -0.1rem;
        transform: scale3d(0, 1, 1);
        width: 100%;
        background-color: var(--color__accent);
        
        }
        
        select:focus + .input-label::after {
        transform: scale3d(1, 1, 1);
        opacity: 1;
        }
        
        select:placeholder-shown + .input-label::before {
        transform: translate3d(0, -2.2rem, 0) scale3d(1, 1, 1);
        }
        
        .input-label::before,
        select:focus + .input-label::before {
        transform: translate3d(0, -3.12rem, 0) scale3d(0.82, 0.82, 1);
        }
        
        select:focus + .input-label::before {
        color: var(--color__accent);
        }
    }

    span.policy-agreement-txt {
        font-family: "Montserrat", sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 11px;
        line-height: 14px;
        letter-spacing: 0.2px;
        width: 410px;
        text-align: left;
        padding-top: 6px;

        .second-line {
            display: block;
        }

        a {
            color: #bb6bd9;
            text-decoration: none;
        }
    }


    .sign-btn-gray {
        margin-top: 6px;
    }
}