.signup {
    font-family: "Montserrat", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;

    width: 1440px;
    height: 100vh;
    margin: 0 auto 0 auto;

    margin-left: auto;
    margin-right: auto;

    position: relative;

    .signup-container {
        display: flex;  
        align-self: stretch;
        justify-content: flex-end;
        height: 100vh;

        .signup-main{
            display: flex;  
            flex-wrap: wrap;
            justify-content: center;
            flex-grow: 1;
            align-content: flex-end;

            .signup-form {
                flex-grow: 1;
            }
            .signup-logo {
                width: 220px;
                padding-left: 53px;
                padding-top: 41px;
                position: absolute;
                left: 0;
                top: 0;
            }    
            
            .social-media-signup{
                padding-bottom: 0px;
            }
        }
    }
}