@import '../_variables.scss';

.features {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 955px;


    @media #{$mq-large} {
        width: 710px;
    }

    @media #{$mq-medium} {
        flex-wrap: wrap;
        width: 500px;
    }

    @media #{$mq-small} {
        width: 400px;
    }

    @media #{$mq-extra-small} {
        width: 190px;
    }

}


