$lg-screen-width-min: 769px;
$sm-screen-width-max: 768px;

.generate-mail-form {
    display: flex;
    flex-direction: column;

    .header-container {

        h1 {
            font-family: Montserrat;
            font-weight: 600;
            font-size: 32px;
        }

        h4 {
            font-family: Montserrat;
            font-weight: 400;
            font-size: 14px;
            letter-spacing: -0.02em;
        }
    }

    @media (min-width: $lg-screen-width-min) {
        .header-container {
            padding-bottom: 80px;

            h1 {
                padding-bottom: 16px;
            }

            h4 {
                line-height: 165%;
            }
        }
    }

    @media (max-width: $sm-screen-width-max) {
        .header-container {
            padding-bottom: 75px;
            align-self: center;
            width: 340px;

            h1 {
                text-align: center;
                padding-bottom: 17px;
            }

            h4 {
                text-align: center;
                line-height: 165%;
            }
        }
    }

    .input-container {
        padding-bottom: 32px;
    }

    .btnClass {
        width: 445px;
        background: linear-gradient(90.04deg, #25158B 0.04%, #A935D6 99.97%);
        color: #FFFFFF;
        text-transform: uppercase;

        padding-top: 20px;
        padding-bottom: 20px;
        border: 0px;
        border-radius: 36px;

        font-family: Montserrat;
        font-weight: 600;
        font-size: 14px;
    }

    .btnClass:focus {
        outline: none;
    }
}