@import '../_variables.scss';

.subscribe-form {
    display: flex;
    justify-content: center;
    width: 565px;
    margin-left: 100px;
    margin-right: 100px;

    @media #{$mq-medium} {
        width: 480px;
     }
  
     @media #{$mq-extra-small} {
        flex-wrap: wrap;
        width: 280px;
     }

    .subscribe-button {
        margin-left: 20px;

        @media #{$mq-extra-small} {
            margin: auto;
         }
    }
}